<template>
  <v-navigation-drawer v-model="isOpen" fixed right width="450px">
    <v-card :loading="loadingData" style="height: 100%">
      <v-toolbar flat>
        <v-toolbar-title>{{ pluginName }} - {{ formType }}</v-toolbar-title>
        <v-spacer />
        <v-icon class="mr-6" color="primary" @click="postConfig" :disabled="!valid">fa-floppy-o</v-icon>
        <v-icon @click="onClose">fa-times-circle</v-icon>
      </v-toolbar>
      <v-divider></v-divider>
      <v-container class="pa-4 mt-4">
        <v-form v-model="valid" ref="form" lazy-validation>
          <yaml-editor
            v-if="formType === formTypes.YML_CONFIG && !loadingData"
            :code="config"
            @input="onEditorValueChange"
          ></yaml-editor>
          <json-editor
            v-if="formType === formTypes.JSON_CONFIG && !loadingData"
            :code="config"
            @input="onEditorValueChange"
          ></json-editor>
          <env-editor
            v-if="formType === formTypes.ENV_CONFIG && !loadingData"
            :code="config"
            @input="onEditorValueChange"
          ></env-editor>
          <txt-editor
            v-if="formType === formTypes.TXT_CONFIG && !loadingData"
            :code="config"
            @input="onEditorValueChange"
          ></txt-editor>
        </v-form>
        <form-error :error="error"></form-error>
      </v-container>
    </v-card>
  </v-navigation-drawer>
</template>

<script>
import FormError from '../Common/FormError.vue';
import EnvEditor from '../Common/EnvEditor.vue';
import YamlEditor from '../Common/YamlEditor.vue';
import JsonEditor from '../Common/JsonEditor.vue';
import {
  ENV_CONFIG,
  JSON_CONFIG,
  TXT_CONFIG,
  YML_CONFIG,
  SERVICE_NAMES,
  RUBIX_APP_TYPES,
} from '../../constants/appConstant';

function resolveConfigFile(appType, pluginName) {
  if (pluginName === SERVICE_NAMES.RUBIX_FLOW_FRAMEWORK || pluginName === SERVICE_NAMES.RUBIX_BROKER) {
    return YML_CONFIG;
  } else if (appType === RUBIX_APP_TYPES.PYTHON_APP) {
    return JSON_CONFIG;
  } else if (appType === RUBIX_APP_TYPES.APT_APP) {
    return TXT_CONFIG;
  } else if (appType === RUBIX_APP_TYPES.FRONTEND_APP) {
    return ENV_CONFIG;
  }
  return YML_CONFIG;
}

export default {
  name: 'config-form',
  props: ['onClose', 'isOpen', 'pluginName', 'fetchConfig', 'updateConfig', 'appType'],
  data: () => ({
    rules: {
      required: value => !!value || 'Required.',
    },
    valid: false,
    loadingData: false,
    config: '',
    newValue: '',
    error: { message: '' },
    formTypes: {
      ENV_CONFIG,
      TXT_CONFIG,
      YML_CONFIG,
      JSON_CONFIG,
    },
  }),

  components: { YamlEditor, FormError, JsonEditor, EnvEditor },
  computed: {
    formType() {
      return resolveConfigFile(this.appType, this.pluginName);
    },
  },
  watch: {
    isOpen(val) {
      if (val) this.getConfigs();
    },
  },
  mounted() {
    this.error.message = '';
    this.getConfigs();
  },
  methods: {
    getConfigs() {
      this.loadingData = true;
      this.fetchConfig(this.pluginName)
        .then(response => {
          this.config = response;
        })
        .catch(() => {
          this.config = '';
        })
        .finally(() => {
          this.loadingData = false;
        });
    },
    postConfig() {
      const postData = { data: this.newValue };
      this.error.message = '';
      this.updateConfig(this.pluginName, postData)
        .then(data => {
          this.$toasted.show(`Config is updated successfully!`);
          this.onClose();
        })
        .catch(ex => {
          this.error.message = 'YAML config incorrect.';
          this.$toasted.show(`Check your YAML config!`, { type: 'error' });
        });
    },
    isCorrectKey(config) {
      return true; // TODO: YAML validator
    },
    onEditorValueChange(config) {
      this.newValue = config;
    },
  },
};
</script>


